import { queryMyCommand, addVipCount } from '@/common/vmeitime-http/index.js'
export default {
	data() {
		return {
			dataList: {},
			bStop: false
		}
	},
	onLoad() {
		this.queryTaskList();
	},
	onShareAppMessage () {
		
		addVipCount({
			vipGetType: '1'
		}).then(() => {
			this.queryTaskList();
		})
		
		return {
			title: this.dataList.title,
			path: '/pages/tabBar/HomePage/HomePage'
		}
	},
	methods: {
		async queryTaskList() {
			const res = await queryMyCommand({});
			this.dataList = res.data.data;
			this.bStop = true;
		}
	}
}
